import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
export default {
	mixins: [MescrollMixin], // 使用mixin (在main.js注册全局组件，内部已注册onPullDownRefresh)
	data() {
		return {
			downOption: {
				native: true // 必须配置此项，且需在pages.json配置"enablePullDownRefresh" : true
			},
			upOption: {
				noMoreSize: 4,
				empty: {
					tip: '~ 暂时没有流水 ~',
					btnText: '去购物'
				},
				page: {
					num: 0,
					size: 20,
					time: null
				},
				textNoMore: "~别再拉了，我是有底线的~"
			},
			tabItem:[],
		}
	},
	onLoad(option) {
		
	},
	methods: {
		upCallback(page) {
			//联网加载数据
			var that = this;
			let orderReq = {
				"pageIndex": page.num,
				"pageSize": page.size
			}
			this.$http.get(this.$httpApi.my.waterBill, orderReq).
			then(function(response) {
				//这里只会在接口是成功状态返回
				that.mescroll.endByPage(response.pageSize, response.totalPage);
				if (page.num == 1) that.tabItem = []; //如果是第一页需手动制空列表
				that.tabItem = that.tabItem.concat(response.list); //追加新数据
			}).catch(function(error) {
				//这里只会在接口是失败状态返回，不需要去处理错误提示
				this.mescroll.endErr();
			});
		},
		//点击空布局按钮的回调
		emptyClick() {
			uni.showToast({
				title: '点击了按钮,具体逻辑自行实现'
			})
		},
		showReason(item){
			if(item.billType == 3 || item.billType == 4 || item.billType ==5){
				const url  = "/pages/order/orderDetail?orderId=" +item.orderId
				uni.navigateTo({
					url
				})
			}
		}
		
	}
}
